
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XInput from 'vux/src/components/x-input/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import Vue from "vue";
import { mapState } from "vuex";
import store from "store";
import sha1 from "sha1";
import _ from "lodash";
import cameraActionsheet from "../../common/cameraActionsheet";
export default {
directives: {
TransferDom
},
created() {
let user = store.get("user");
this.user = _.assign(this.user, user);
},
data() {
return {
user: {
breed: "",
age: "",
sex: "0"
},
isShow: false,
menus: {
album: "相册",
takePhoto: "拍照"
},
timestamp: "",
avatarData: "",
isUpload: false,
precent: 0,
isAvatar: false,
editVistable: false,
isSubmit: false,
imageOption: {
destinationType: 0, //base64
}
};
},
components: {
Card,
Group,
Cell,
CellBox,
Blur,
cameraActionsheet,
XCircle,
Popup,
XInput,
Checker,
CheckerItem,
XButton
},
methods: {

showEdit() {
this.editVistable = true;
},
_closeEdit() {
this.editVistable = false;
},
submitEdit() {
let user = this.user;
if (user && user.accessToken) {
let body = {
accessToken: user.accessToken,
age: user.age,
breed: user.breed,
nickname: user.nickname,
sex: user.sex
};
this.asyncUser(body);
}
},

logout(){
this.$store.commit("USER_LOGOUT");
this.user={};
this.$router.replace({name:"login"});
},

choosePhoto() {
this.isShow = true;
},
cameraSuccess(url) {
url = "data:image/jpeg;base64," + url; //base64
this.avatarData = url;
this.uploadAvatar();
},
cameraError(msg) {
console.log(msg);
},


uploadAvatar() {
this.isUpload = true;
this.timestamp = Date.now();

let signatureUrl = this.config.url.base + this.config.url.imageSignature;








let body = {
accessToken: this.user.accessToken,
type: "avatar",
cloud:"qiniu"
};
let param = {
api: signatureUrl,
body: body,
cb: this.cbSignature
};
this.$store.dispatch("getSignature", param);
},

dataURItoBlob(dataURI) {

var byteString;
if (dataURI.split(',')[0].indexOf('base64') >= 0)
byteString = atob(dataURI.split(',')[1]);
else
byteString = unescape(dataURI.split(',')[1]);

var mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

var ia = new Uint8Array(byteString.length);
for (var i = 0; i < byteString.length; i++) {
ia[i] = byteString.charCodeAt(i);
}
console.log(mimeString);
return new Blob([ia], {type:mimeString});
},

cbSignature(data) {
if (data.success) {
let signature = data.obj.signature;
let body = new FormData();
let imageURL = "";
if(data.obj.key)
{

let key = data.obj.key;
body.append("key", key);
body.append("token", signature);
body.append("file", this.dataURItoBlob(this.avatarData));
imageURL = this.config.qiniu.upload;
}
else
{

let folder = data.obj.folder;
let tags = data.obj.tags;
body.append("folder", folder);
body.append("signature", signature);
body.append("timestamp", this.timestamp);
body.append("tags", tags);
body.append("api_key", this.config.cloudinary.api_key);
body.append("resource_type", "image");
body.append("file", this.avatarData);
imageURL = this.config.cloudinary.apiBase + this.config.cloudinary.image;
}
let param = {
api: imageURL,
body: body,
header: {
"Content-Type": "multipart/form-data"
},
cb: this.cbuploadAvatar,
uploading: this.onUploading
};
this.$store.dispatch("uploadImage", param);
}
else
{
this.$vux.toast.show({
text:data.obj.errorMsg,
type:"warn"
})
}
},

onUploading(progressEvent) {
let that = this;
let loaded = progressEvent.loaded,
total = progressEvent.total;
that.$nextTick(() => {
that.precent = Math.round(loaded / total * 100);
});
},



















cbuploadAvatar(data) {
if (data.public_id) {
this.user.avatar = data.url;
}
else
{

this.user.avatar = this.config.qiniu.fileUrl + "/" +data.key;
}
let user = this.user;

if (user && user.accessToken) {
let body = {
accessToken: user.accessToken,
avatar: user.avatar
};
this.asyncUser(body);
this.isAvatar = true;
}
this.isUpload = false;
this.precent = 0;
},

asyncUser(body) {
let url = this.config.url.base + this.config.url.update;
let params = {
api: url,
body: body,
cb: this.cbUserUpdate
};
this.$store.dispatch("updateUser", params);
},

cbUserUpdate(data) {
if (data.success) {
if(data.obj.isempty)
{
this.$vux.toast.show({
text:data.obj.tips,
type:"warn"
})
return;
}
if (this.isAvatar) {
this.$vux.toast.show({
text: "头像更新成功",
type: "success"
});
this.isAvatar = false;
}
else{
this._closeEdit();
}
this.user = _.assign(this.user, data.obj);
this.user.sex = this.user.sex.toString();
this.$store.commit("UPDATE_USER_ALL", this.user);
}
else{
this.$vux.toast.show({
text:data.obj.errorMsg,
type:"warn"
})
}
}
},
computed: {
...mapState({
config: state => state.config
})
}
};



