
import LoadMore from 'vux/src/components/load-more/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import videoRow from "./videoRow"
import { mapState } from "vuex";
import axios from "axios";
import Mock from "mockjs";
import queryString from "query-string";
import store from "store";
export default {
created() {
this._initStore();
this._getVideoLists(0,'down');
},
mounted() {
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top : 0});
this.$refs.scrollerBottom.donePulldown();
});
},
methods: {

_initStore() {
store.set("nextPage", 1);
store.set("total", 0);
},
_getVideoLists(page, type) {

this.onFetching = true;

let url = this.config.url.base + this.config.url.videoList;
axios
.get(
url +
"?" +
queryString.stringify({ accessToken: "11dsdf", page: page })
)
.then(response => {
response = Mock.mock(response.data);
if (response.success) {
if ("up" == type) {
this.videoLists = this.videoLists.concat(response.data);
}

if ("down" == type) {
this.videoLists = response.data;
}

store.set("total", response.total);
store.set("nextPage", ++page);
store.set("videoLists", this.videoLists);
}
this.onFetching = false;

})
.catch(error => {
this.onFetching = false;
let videoLists = store.get("videoLists");
this.videoLists = videoLists ? videoLists : [];
console.log(error);
});
},

_hasMore() {
return this.videoLists.length < store.get("total");
},

onScrollBottom() {
if (!this._hasMore()) {
this.$refs.scrollerBottom.reset();
this.$refs.scrollerBottom.disablePullup();
return;
}
if (this.onFetching) return;
let nextPage = store.get("nextPage");
if (!nextPage) {
nextPage = 1;
this._initStore();
}
this._getVideoLists(nextPage,'up');
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
this.$refs.scrollerBottom.donePullup();
});
},

refreshData() {
if (this.onFetching) {
return;
} else {
this._initStore();
this._getVideoLists(0,'down');
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top : 0});
this.$refs.scrollerBottom.donePulldown();
});
}
}
},
data() {
return {
videoLists: [],
onFetching: false,
pulldownConfig: {
content: "下拉刷新",
downContent: "下拉刷新",
upContent: "释放刷新",
loadingContent: "正在加载..."
},
pullupConfig: {
upContent: " ",
downContent: " ",
loadingContent: " "
}
};
},
computed: {
...mapState({
config: state => state.config
})
},
components: {
videoRow,
Scroller,
LoadMore
}
};



