export default {
  props: ["src","options"],
  mounted () {
    this.screenW = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
    this.video = this.$refs.myvideo;
    let that = this;
    this.video.addEventListener('canplaythrough',function(){
        that.isLoaded = true;
        if(that.options.playStatus && "autoplay"==that.options.playStatus){
            that.playVideo();
        }
    });
  },
  data(){
    return {
      video:null,
      screenW: 0,
      isPlay:false,
      isPause:false,
      precent: 0,
      isLoaded:false
    }
  },
  methods: {
      //播放视频
    playVideo(){
      this.isPlay = true;
      this.isPause = false;
      this.video.play();
      if(!this.isPlay)
      {
        this.precent = 0;
      }
      this.video.addEventListener('playing', ()=>{
        this.$emit("playing");
      })
      this.video.addEventListener('waiting', ()=>{
        this.$emit("waiting");
      })
      this.video.addEventListener('play', ()=>{
        this.$emit("play");
      })
      this.video.addEventListener('timeupdate', this.timeline)
      this.video.addEventListener('ended', (e) => {
            this.isPlay = false;
            this.isPause = false;
            this.precent = 100;
            this.$emit("videoEnd");
        })
    },
    pauseVideo(){
      this.isPause = true;
      this.video.pause();
    },
    timeline () {
        this.precent = Math.floor((this.video.currentTime / this.video.duration)*100)
        this.$emit("process",this.precent);
    },
  }
}



