
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapState } from "vuex";
import axios from "axios";
import Mock from "mockjs";
import queryString from "query-string";
export default {
props: ["item"],
methods: {

like() {
let url = this.config.url.base + this.config.url.like;
let isLike = 0 == this.item.isLike ? 1 : 0;
axios
.get(
url +
"?" +
queryString.stringify({ accessToken: "11dsdf", isLike: isLike })
)
.then(response => {
response = Mock.mock(response);
if (response.data.success) {
this.item.isLike = isLike;
}
})
.catch(error => {
console.log(error);
});
},

goDetail(row){
this.$router.push({name:'videoDetail',params:{video:row}});
},

comment(row){

}
},
components: {
Card,
Flexbox,
FlexboxItem
},
computed: {
...mapState({
config: state => state.config,
route: state => state.route
})
}
};



