
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapState } from "vuex";
export default {
components: {
XInput,
XButton,
Countdown,
Toast,
Group
},
computed: {
...mapState({
config: state => state.config
})
},
data() {
return {
isVerify: false,
isLogin: false,
step: 1,
phoneNumber: "",
verifyCode: "",
codeTime: 60
};
},
methods: {

doLogin() {
if (
!this.phoneNumber ||
Object.keys(this.$refs.phoneNumber.errors).length > 0
) {
this.$vux.toast.text("手机号格式不正确！", "middle");
return;
}
if (!this.verifyCode || Object.keys(this.$refs.code.errors).length > 0) {
this.$vux.toast.text("验证码不正确！", "middle");
return;
}
this.isLogin = true;
let params = {};
params.api = this.config.url.base + this.config.url.doLogin;
params.body = {
phoneNumber: this.phoneNumber,
verifyCode: this.verifyCode
};
params.doLogin = this.doLoginCallback;
this.$store.dispatch("doLogin", params);
},

submitVerify() {
if (
!this.phoneNumber ||
Object.keys(this.$refs.phoneNumber.errors).length > 0
) {
this.$vux.toast.text("手机号格式不正确！", "middle");
return;
}
this.isVerify = true;
this.step = 2;
let params = {};
params.api = this.config.url.base + this.config.url.verifyCode;
params.body = {
phoneNumber: this.phoneNumber
};
params.getVerifyCode = this.getVerifyCode;
this.$store.dispatch("getVerifyCode", params);
},

doLoginCallback(data) {

if (data.success) {
this.isLogin = false;
let that = this;
this.$vux.toast.show({
text:"登陆成功",
type:"success",
onHide:()=>{
that.$router.replace({ name: "user" });
}
});
this.$store.commit("UPDATE_USER_ALL", data.obj);
}
},

getVerifyCode(data) {
if(data.success)
{
console.log(data);
}
else{
this.$vux.toast.show({
text:"验证码获取错误！"+data.obj.errorMsg,
type:"error"
});
}
},
countFinish() {
this.isVerify = false;
this.codeTime = 60;
}
}
};



